<template>
    <v-container class="fill-height align-start">
        <v-row>
            <v-col cols="12">
                <v-card>
                    <v-card-title>  {{botName}}</v-card-title>
                    <v-card-text>
                        <v-form autocomplete="off">
                            <v-combobox v-model="needsSubscription"
                                label=" "
                                multiple
                                chips
                                deletable-chips
                                hint=": @vcblr"
                                persistent-hint
                            ></v-combobox>
                            <v-select v-model="settings.botType"
                                class="mt-4"
                                label=" "
                                :items="botTypes"
                            ></v-select>
                            <v-switch v-model="settings.useCustomCategories" v-if="settings.botType === 'vacancies'"
                                label="  "
                            ></v-switch>
                            <v-switch v-model="settings.restrictToBot" v-if="settings.botType === 'vacancies'"
                                    label="       "
                            ></v-switch>
                        </v-form>
                    </v-card-text>
                    <v-card-actions>
                        <v-btn @click="gotoList"> </v-btn>
                        <v-btn large color="primary" @click="save"></v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    export default {
        name: "BotSettings",
        data() {
            return {
                settings: {},
                needsSubscription: [],
                defaultSettings: {},
                botTypes: [
                    {text: ' ', value: 'books'},
                    {text: '  -', value: 'business'},
                    {text: '  ', value: 'coupons'},
                    {text: '', value: 'dating'},
                    {text: ' ', value: 'disease'},
                    {text: ' ', value: 'familyname'},
                    {text: '  ', value: 'film'},
                    {text: '  ()', value: 'finance'},
                    {text: '  ()', value: 'fincat'},
                    {text: '', value: 'horoscope'},
                    {text: ' ', value: 'linker'},
                    {text: ' ', value: 'orfo'},
                    {text: '', value: 'pdd'},
                    {text: '', value: 'podcasts'},
                    {text: '', value: 'present'},
                    {text: '  ', value: 'symptoms'},
                    {text: '  ', value: 'todo'},
                    {text: '', value: 'vacancies'},
                    {text: '', value: 'wikipedia'},
                    {text: ' ', value: 'ytdlc'},
                ],
            }
        },
        async created() {
            if (this.botName) {
                await this.$store.dispatch('loadSettings', this.botName);
                this.initSettings();
            }
        },
        watch: {
            botName() {
                this.$store.dispatch('loadSettings', this.botName);
            },
            storeSettings() {
                this.initSettings();
            },
        },
        methods: {
            initSettings() {
                if (this.storeSettings) {
                    this.settings = this.storeSettings;
                    this.needsSubscription = this.settings.needsSubscription instanceof Array
                        ? this.settings.needsSubscription
                        : [ this.settings.needsSubscription ];
                }
                else {
                    this.settings = this.defaultSettings;
                }
            },
            async save() {
                this.settings.botName = this.botName;
                this.settings.needsSubscription = this.needsSubscription;
                await this.$store.dispatch('saveSettings', this.settings);
                this.gotoList();
            },
            gotoList() {
                this.$router.push({name: 'stats'});
            }
        },
        computed: {
            botName() {
                return (this.$route.params && this.$route.params.botName) || false;
            },
            storeSettings() {
                return this.$store.state.bots.settings;
            },
        }
    }
</script>

